
Template.__checkName("followUsers");
Template["followUsers"] = new Template("Template.followUsers", (function() {
  var view = this;
  return HTML.FORM({
    class: "form-inline"
  }, HTML.Raw(' \n\t\t<input type="text" class="form-control" id="searchUser" placeholder="Search for user">\n\t\t<button type="submit" class="btn btn-info">Search</button>\n\n\t\t'), Blaze.If(function() {
    return Spacebars.call(view.lookup("foundUser"));
  }, function() {
    return [ "\n\t\t\t", HTML.DIV({
      class: "found-user"
    }, "\n\t\t\t\t", HTML.BUTTON({
      type: "button",
      class: "btn btn-default",
      id: "follow"
    }, "Follow @", Blaze.View("lookup:foundUser.username", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("foundUser"), "username"));
    })), "\n\t\t\t"), "\n\t\t" ];
  }), "\n  \t\n\t  \t", HTML.DIV({
    class: "recommend-users"
  }, " \n\t\t  ", HTML.Raw("<h5>Who to follow:</h5>"), "\n\t\t  ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("recommendedUsers"));
  }, function() {
    return [ "\n\t\t    ", HTML.BUTTON({
      type: "button",
      class: "btn btn-default",
      id: "followRec"
    }, "Follow @", Blaze.View("lookup:..username", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "username"));
    })), "\n\t\t  " ];
  }), "\n\t\t"), "\n\t\t\n\t");
}));
